@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500;600&display=swap');
*{
    margin: 0;
    padding:0;
    font-family: 'Poppins', sans-serif;
    
}
.navibar{
    transform: translateY(190px);
}
.sec{
    min-height: 100vh;
    width: 100%;
    background-image: url("alchemist1.jpg");
    background-position: 55% 90%;
    background-size: cover;
    position: relative;

    
 
}
.name{
    position: relative;
    text-align: center;
    top: 210px;
    color: antiquewhite;
    font-size: 66px;
    
   
}
.who{
    position: relative;
    text-align: center;
    top: 180px;
    color: aliceblue;
    font-size: 30px;
}
.icon{
    text-align: center;
    transform: translateY(190px);


}
.in{
    width: 30px;
    height: 22px;
    padding: 5px;
    color: rgb(162, 158, 154);
    margin: 10px;
}
.in:hover{
    color: antiquewhite;
    transform: scale(1.2);
}

